.song-container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  img {
    width: 20%;
    border-radius: 50%;
  }
  h2 {
    padding: 2rem 1rem 1rem 1rem;
    font-family: "Lato", sans-serif;
    color: white;
  }
  h3 {
    font-size: 1rem;
    font-family: "Lato", sans-serif;
    color: white;
  }
}
